@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{margin:0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.employees-title{font-size:1.25rem;font-weight:600;margin-bottom:1.5rem;margin-top:1.5rem;border-bottom:1px solid black;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}

.employee__card{cursor:pointer;padding:10px 15px}.employee__link{text-decoration:none;cursor:pointer;padding:0.5rem;color:#1a76d2}.employee__link:hover{background-color:#f5f5f5;border-radius:0.25rem}

.user-data-table-container{display:flex;justify-content:center;width:100%;margin-bottom:8px}.user-data-table{display:flex;align-items:center;-webkit-font-feature-settings:"smcp";font-feature-settings:"smcp";font-variant:small-caps;max-width:1200px;width:100%;background-color:#f5f5f5;font-weight:700;border-radius:4px;padding:8px}.user-data-table__info{margin-left:8px}.user-data-table__info--name{margin-left:16px}

.form__warningMessage{display:flex;align-items:center;justify-content:center;color:red;text-align:center;height:3rem}

.left-menu{width:100%;height:100%}.left-menu__nav-item{color:black;text-decoration:none;width:100%}

.layout{width:100vw;height:100vh}.layout__menu-container{position:fixed;top:0;bottom:0;left:0;background-color:#f5f5f5;width:14rem;border-right:2px solid #e3e3e3}.layout__content{width:calc(100% - 14rem);height:100vh;margin-left:14rem;padding:1rem;overflow-x:hidden}

